"use client";

import { useEffect, useState, useCallback } from "react";
import { motion } from "framer-motion";
import { useRouter } from "next/navigation";
import { useAuthStore } from "../store/authStore";
import { useProfileStore } from "../store/profileStore";
import { graphApi, profileApi } from "../lib/api";
import { GraphNode, GraphLink, Recommendation } from "../lib/types";
import { GitBranch, Info, Star, AlertTriangle, Circle, ArrowRight } from "lucide-react";

const TIER_NAMES = ["", "Foundations", "Core DS", "Non-Linear DS", "Core Algorithms", "Advanced Algorithms"];
const TIER_COLORS = ["", "#22c55e", "#3b82f6", "#8b5cf6", "#f59e0b", "#ef4444"];

export default function GraphPage() {
    const router = useRouter();
    const { isAuthenticated } = useAuthStore();
    const { profile, setProfile } = useProfileStore();
    const [nodes, setNodes] = useState<GraphNode[]>([]);
    const [recs, setRecs] = useState<Recommendation[]>([]);
    const [selected, setSelected] = useState<GraphNode | null>(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        if (!isAuthenticated) { router.push("/login"); return; }
        (async () => {
            try {
                const [conceptsRes, profileRes, recsRes] = await Promise.all([
                    graphApi.concepts(),
                    profile ? Promise.resolve({ data: profile }) : profileApi.get(),
                    graphApi.recommendations().catch(() => ({ data: [] })),
                ]);
                if (!profile) setProfile(profileRes.data);
                const prof = profileRes.data;
                const enriched: GraphNode[] = (conceptsRes.data || []).map((c: any) => ({
                    id: c.id,
                    name: c.name,
                    tier: c.tier,
                    skill: prof?.skills?.[c.name] ?? 0,
                    isGap: prof?.gaps?.includes(c.name) ?? false,
                    isStrength: prof?.strengths?.includes(c.name) ?? false,
                }));
                setNodes(enriched);
                setRecs(recsRes.data?.slice(0, 5) || []);
            } catch {
                // silently handle
            } finally {
                setLoading(false);
            }
        })();
    }, [isAuthenticated, router, profile, setProfile]);

    const getNodeColor = (n: GraphNode) => {
        if (n.isStrength) return "#22c55e";
        if (n.isGap) return "#ef4444";
        if (n.skill && n.skill > 0.5) return "#f97316";
        if (n.skill && n.skill > 0) return "#f59e0b";
        return "#9ca3af";
    };

    const getTierNodes = (tier: number) => nodes.filter((n) => n.tier === tier);

    return (
        <div className="min-h-screen bg-[#faf9f6] pt-16">
            <div className="max-w-7xl mx-auto px-6 py-10">
                {/* Header */}
                <motion.div
                    className="mb-8"
                    initial={{ opacity: 0, y: 20 }}
                    animate={{ opacity: 1, y: 0 }}
                >
                    <div className="flex items-center gap-2 mb-3">
                        <GitBranch className="w-5 h-5 text-[#f97316]" />
                        <span className="text-xs font-bold uppercase tracking-widest text-[#f97316]">Knowledge Graph</span>
                    </div>
                    <h1 className="font-serif font-black text-4xl md:text-5xl text-[#0d0d0d] mb-3">
                        Your Learning Map
                    </h1>
                    <p className="text-[#6b7280]">
                        30 DSA concepts organized by prerequisites. Color = your mastery.
                    </p>
                </motion.div>

                {loading ? (
                    <div className="flex justify-center py-20">
                        <div className="w-8 h-8 border-2 border-[#f97316] border-t-transparent rounded-full animate-spin" />
                    </div>
                ) : (
                    <div className="grid lg:grid-cols-4 gap-6">
                        {/* Graph visualization — tier columns */}
                        <div className="lg:col-span-3 bg-white border border-[#e5e7eb] rounded-2xl p-6 overflow-auto">
                            <div className="flex flex-col gap-8 min-w-0">
                                {[1, 2, 3, 4, 5].map((tier) => (
                                    <div key={tier}>
                                        <div className="flex items-center gap-2 mb-3">
                                            <div
                                                className="w-3 h-3 rounded-full"
                                                style={{ background: TIER_COLORS[tier] }}
                                            />
                                            <span className="text-xs font-bold uppercase tracking-wider text-[#6b7280]">
                                                Tier {tier} — {TIER_NAMES[tier]}
                                            </span>
                                        </div>
                                        <div className="flex flex-wrap gap-2">
                                            {getTierNodes(tier).map((node, i) => (
                                                <motion.button
                                                    key={node.id}
                                                    onClick={() => setSelected(selected?.id === node.id ? null : node)}
                                                    className={`relative px-3 py-2 rounded-lg text-xs font-semibold border-2 transition-all ${selected?.id === node.id ? "scale-105 shadow-lg" : "hover:scale-102"
                                                        }`}
                                                    style={{
                                                        borderColor: getNodeColor(node),
                                                        background: selected?.id === node.id
                                                            ? getNodeColor(node) + "22"
                                                            : getNodeColor(node) + "11",
                                                        color: getNodeColor(node),
                                                    }}
                                                    initial={{ opacity: 0, scale: 0.8 }}
                                                    animate={{ opacity: 1, scale: 1 }}
                                                    transition={{ delay: i * 0.03 }}
                                                    whileHover={{ scale: 1.05 }}
                                                    whileTap={{ scale: 0.97 }}
                                                >
                                                    {node.name}
                                                    {node.skill !== undefined && node.skill > 0 && (
                                                        <span className="ml-1.5 opacity-70 text-[10px]">
                                                            {Math.round(node.skill * 100)}%
                                                        </span>
                                                    )}
                                                </motion.button>
                                            ))}
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>

                        {/* Right sidebar */}
                        <div className="flex flex-col gap-4">
                            {/* Legend */}
                            <div className="bg-white border border-[#e5e7eb] rounded-2xl p-5">
                                <h3 className="font-semibold text-sm text-[#374151] mb-3 uppercase tracking-wider">Legend</h3>
                                <div className="flex flex-col gap-2 text-xs">
                                    {[
                                        { color: "#22c55e", label: "Strength" },
                                        { color: "#ef4444", label: "Gap" },
                                        { color: "#f97316", label: "Learning" },
                                        { color: "#f59e0b", label: "Partial" },
                                        { color: "#9ca3af", label: "Not started" },
                                    ].map((l) => (
                                        <div key={l.label} className="flex items-center gap-2">
                                            <div className="w-3 h-3 rounded-full" style={{ background: l.color }} />
                                            <span className="text-[#6b7280]">{l.label}</span>
                                        </div>
                                    ))}
                                </div>
                            </div>

                            {/* Selected node detail */}
                            {selected && (
                                <motion.div
                                    className="bg-white border border-[#e5e7eb] rounded-2xl p-5"
                                    initial={{ opacity: 0, y: 10 }}
                                    animate={{ opacity: 1, y: 0 }}
                                >
                                    <div className="flex items-center gap-2 mb-3">
                                        <div className="w-3 h-3 rounded-full" style={{ background: getNodeColor(selected) }} />
                                        <Info className="w-3.5 h-3.5 text-[#9ca3af]" />
                                    </div>
                                    <h3 className="font-serif font-bold text-lg text-[#0d0d0d] mb-1">{selected.name}</h3>
                                    <p className="text-xs text-[#9ca3af] mb-3">Tier {selected.tier} — {TIER_NAMES[selected.tier]}</p>
                                    {selected.isStrength && (
                                        <div className="flex items-center gap-1.5 text-xs text-green-600 mb-2">
                                            <Star className="w-3 h-3" /> Strength area
                                        </div>
                                    )}
                                    {selected.isGap && (
                                        <div className="flex items-center gap-1.5 text-xs text-red-600 mb-2">
                                            <AlertTriangle className="w-3 h-3" /> Known gap
                                        </div>
                                    )}
                                    {selected.skill !== undefined && (
                                        <div>
                                            <div className="text-xs text-[#6b7280] mb-1">Mastery</div>
                                            <div className="h-2 bg-[#f3f4f6] rounded-full overflow-hidden">
                                                <div
                                                    className="h-full rounded-full"
                                                    style={{ width: `${(selected.skill || 0) * 100}%`, background: getNodeColor(selected) }}
                                                />
                                            </div>
                                            <div className="text-xs text-right mt-0.5 text-[#9ca3af]">
                                                {Math.round((selected.skill || 0) * 100)}%
                                            </div>
                                        </div>
                                    )}
                                </motion.div>
                            )}

                            {/* Recommendations */}
                            {recs.length > 0 && (
                                <div className="bg-[#0d0d0d] rounded-2xl p-5">
                                    <h3 className="font-semibold text-xs text-[#f97316] uppercase tracking-wider mb-3">
                                        Recommended Next
                                    </h3>
                                    <div className="flex flex-col gap-2">
                                        {recs.map((r) => (
                                            <div key={r.concept_id} className="flex items-center gap-2 text-xs text-white/70">
                                                <ArrowRight className="w-3 h-3 text-[#f97316] shrink-0" />
                                                <span>{r.concept_name}</span>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            )}
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}
